import React, { createContext, useContext, useState } from 'react';

const PollingPauseContext = createContext();

export const usePollingPause = () => {
  const context = useContext(PollingPauseContext);
  if (!context) {
    throw new Error('usePollingPause must be used within a PollingPauseProvider');
  }
  return context;
};

export const PollingPauseProvider = ({ children }) => {
  const [isPollingPaused, setIsPollingPaused] = useState(false);

  const pausePolling = () => setIsPollingPaused(true);
  const resumePolling = () => setIsPollingPaused(false);

  const value = {
    isPollingPaused,
    pausePolling,
    resumePolling,
  };

  return (
    <PollingPauseContext.Provider value={value}>
      {children}
    </PollingPauseContext.Provider>
  );
};

