import { jwtDecode } from 'jwt-decode';

import {
  request
} from './utils';
import {
  login as loginRequest,
  refreshToken as refreshTokenRequest,
} from './index';

class AuthService {
  constructor() {
    this.authToken = sessionStorage.getItem('token') || null;
    this.userInfo = this.getUserInfoFromToken(this.authToken);
  }

  setToken(token) {
    this.authToken = token;
    sessionStorage.setItem('token', token);
  }

  async makeAuthRequest(endpoint, options = {}, timeout = 5000) {
    if (!this.authToken) {
      console.warn('No auth token set');
    }

    try {
      const {
        headers: optionHeaders = {},
        ...restOptions
      } = options;

      return await request(endpoint, {
        ...restOptions,
        headers: {
          ...optionHeaders,
          Authorization: `Bearer ${this.authToken}`,
        },
      }, timeout);
    } catch (error) {
      if (error.message === 'Request timed out') {
        // Handle timeout specifically if needed
        console.error('Request timed out:', endpoint);
      }
      if (error.message === 'UNAUTHORIZED') {
        this.logout({ forceReload: true });
      }
      throw error;
    }
  }

  getUserInfoFromToken(token) {
    if (!token) {
      return {};
    }
    // Decode the JWT to get the user info
    const decodedToken = jwtDecode(token);
    const {
      un: username,
      sub: userId,
      roles,
    } = decodedToken;

    const userInfo = {
      username,
      userId,
      roles,
    };

    this.userInfo = userInfo;

    return userInfo;
  }

  async login(username, password) {
    const response = await loginRequest(username, password);
    if (response.access_token) {
          this.setToken(response.access_token);
        }

    return this.authToken;
  }

  logout({ forceReload = false } = {}) {
    this.authToken = null;
    this.userInfo = {};
    sessionStorage.removeItem('token');
    if (forceReload)
      window.location.reload();
    // Add any other logout logic here
  }

  async refreshToken() {
    const response = await refreshTokenRequest();
    this.setToken(response.access_token);
    return this.authToken;
  }
}

export default new AuthService();
