import { createSlice, createAsyncThunk } from '@reduxjs/toolkit';
import { executeIperf as runIperfService } from '../../service';

export const runSpeedTest = createAsyncThunk(
  'performance/runTest',
  async (ip) => {
    const result = await runIperfService(ip);
    return result;
  }
);

const performanceSlice = createSlice({
  name: 'performance',
  initialState: {
    testResult: null,
    running: false,
    error: null,
  },
  reducers: {
    clearResults: (state) => {
      state.testResult = null;
      state.error = null;
    },
  },
  extraReducers: (builder) => {
    builder
      .addCase(runSpeedTest.pending, (state) => {
        state.running = true;
      })
      .addCase(runSpeedTest.fulfilled, (state, action) => {
        state.testResult = action.payload;
        state.running = false;
        state.error = null;
      })
      .addCase(runSpeedTest.rejected, (state, action) => {
        state.running = false;
        state.error = action.error.message;
      });
  },
});

export const { clearResults } = performanceSlice.actions;
export default performanceSlice.reducer; 