import React, { createContext, useContext, useState, useEffect, useRef } from 'react';
import { jwtDecode } from 'jwt-decode';
import _ from 'lodash';
import authService from '../service/authService';
import useVpnStatusQuery from '../hooks/useVpnStatusQuery';
import useTokenExpirationQuery from '../hooks/useTokenExpirationQuery';
import { usePollingPause } from './PollingPauseContext';

const AuthContext = createContext(null);

export const AuthProvider = ({ children }) => {
  const [authToken, setAuthToken] = useState(authService.authToken || null);
  const [userInfo, setUserInfo] = useState(authService.userInfo || null);
  const [logoutReason, setLogoutReason] = useState(null);
  const lastActivityRef = useRef(Date.now());
  const [vpnUnavailable, setVpnUnavailable] = useState(false);
  const { isPollingPaused } = usePollingPause();

  // Use React Query for VPN status with polling and retries
  const { data: vpnStatus, error: vpnError, isLoading: vpnLoading } = useVpnStatusQuery({ 
    pausePolling: isPollingPaused 
  });

  // Use React Query for token expiration checking
  const { data: tokenExpirationData, error: tokenExpirationError } = useTokenExpirationQuery(authToken, lastActivityRef);

  // Update VPN availability based on React Query results
  useEffect(() => {
    if (vpnLoading) {
      // Don't change vpnUnavailable while loading to avoid flickering
      return;
    }

    if (vpnError) {
      console.log(' VPN status error - setting vpnUnavailable to true');
      setVpnUnavailable(true);
      return;
    }

    if (!vpnStatus) {
      console.log(' No VPN status data - setting vpnUnavailable to true');
      setVpnUnavailable(true);
      return;
    }

    console.log(' VPN status response:', vpnStatus);
    
    // Check if VPN is available
    // The VPN is considered unavailable if:
    // 1. There's an explicit error in the response (new error format from backend)
    // 2. The response is empty or doesn't have expected structure
    if (vpnStatus.error) {
      console.log(' VPN has explicit error - setting vpnUnavailable to true');
      setVpnUnavailable(true);
    } else if (!vpnStatus || typeof vpnStatus !== 'object') {
      console.log(' VPN response is invalid - setting vpnUnavailable to true');
      setVpnUnavailable(true);
    } else {
      // VPN service is responding, even if there are no peers
      console.log(' VPN is available - setting vpnUnavailable to false');
      setVpnUnavailable(false);
    }
  }, [vpnStatus, vpnError, vpnLoading]);

  // Handle token expiration based on React Query results
  useEffect(() => {
    if (!tokenExpirationData) return;

    const { isExpired, isInactive, needsRefresh } = tokenExpirationData;

    if (isExpired || isInactive) {
      console.log('Session expired due to inactivity or token expiration');
      logout();
      setLogoutReason(isInactive ? 'Session expired due to inactivity' : 'Token expired');
      return;
    }

    if (needsRefresh) {
      console.log('Token approaching expiration, refreshing...');
      handleTokenRefresh();
    }
  }, [tokenExpirationData]);

  // Function to refresh the token
  const handleTokenRefresh = async () => {
    console.log('Refreshing token');
    try {
      const token = await authService.refreshToken();
      setAuthToken(token);
      const userInfo = authService.getUserInfoFromToken(token);
      setUserInfo(userInfo);
    } catch (error) {
      console.error('Token refresh failed:', error);
      logout();
    }
  };

  // Set up event listeners for user activity
  useEffect(() => {
    const events = ['mousedown', 'keydown', 'scroll', 'mousemove', 'touchstart'];
    
    const debouncedActivity = _.debounce(() => {
      const now = Date.now();
      lastActivityRef.current = now;
    }, 10000);

    events.forEach(event => {
      window.addEventListener(event, debouncedActivity);
    });

    return () => {
      events.forEach(event => {
        window.removeEventListener(event, debouncedActivity);
      });
      debouncedActivity.cancel();
    };
  }, []);

  const login = async (username, password) => {
    const token = await authService.login(username, password);
    setAuthToken(token);
    const userInfo = authService.getUserInfoFromToken(token);
    setUserInfo(userInfo);
  };

  const logout = () => {
    setAuthToken(null);
    setUserInfo(null);
    authService.logout();
    setLogoutReason("User logged out");
  };

  return (
    <AuthContext.Provider value={{
      authToken,
      userInfo,
      login,
      logout,
      logoutReason,
      vpnUnavailable,
    }}>
      {children}
    </AuthContext.Provider>
  );
};

export const useAuth = () => useContext(AuthContext);