import authService from './authService';

const LICENSE_STATUS_CACHE_KEY = 'warhammer/license-status';

const isBrowser = () => typeof window !== 'undefined' && typeof window.localStorage !== 'undefined';

const readLicenseStatusCache = () => {
  if (!isBrowser()) {
    return null;
  }

  try {
    const cachedRaw = window.localStorage.getItem(LICENSE_STATUS_CACHE_KEY);
    if (!cachedRaw) {
      return null;
    }

    const cached = JSON.parse(cachedRaw);
    if (!cached || !cached.data) {
      return null;
    }

    const now = Date.now();
    if (cached.revalidateAt && now < cached.revalidateAt) {
      return cached.data;
    }
  } catch (error) {
    console.warn('Failed to read cached license status:', error);
  }

  return null;
};

const storeLicenseStatusCache = (data) => {
  if (!isBrowser()) {
    return;
  }

  try {
    const now = Date.now();
    const minTtl = 60 * 1000; // 1 minute
    const defaultTtl = 5 * 60 * 1000; // 5 minutes
    const maxTtl = 12 * 60 * 60 * 1000; // 12 hours

    let ttl = defaultTtl;

    if (data?.status === 'licensed') {
      const expiry = data?.license_info?.expiry_date
        ? Date.parse(data.license_info.expiry_date)
        : NaN;

      if (!Number.isNaN(expiry)) {
        const timeUntilExpiry = expiry - now;
        if (timeUntilExpiry > 0) {
          const buffer = 5 * 60 * 1000; // refresh 5 minutes before expiry
          ttl = Math.min(
            maxTtl,
            Math.max(minTtl, timeUntilExpiry - buffer)
          );
        }
      }
    } else {
      // Short cache for non-licensed states to detect upgrades quickly
      ttl = defaultTtl;
    }

    const payload = {
      data,
      cachedAt: now,
      revalidateAt: now + ttl,
    };

    window.localStorage.setItem(LICENSE_STATUS_CACHE_KEY, JSON.stringify(payload));
  } catch (error) {
    console.warn('Failed to cache license status:', error);
  }
};

export const clearLicenseStatusCache = () => {
  if (!isBrowser()) {
    return;
  }

  try {
    window.localStorage.removeItem(LICENSE_STATUS_CACHE_KEY);
  } catch (error) {
    console.warn('Failed to clear license status cache:', error);
  }
};

// License status endpoint with localStorage caching
export const getLicenseStatus = async () => {
  const cached = readLicenseStatusCache();
  if (cached) {
    return cached;
  }

  const response = await authService.makeAuthRequest('license/status');
  storeLicenseStatusCache(response);
  return response;
};

// License information endpoint
export const getLicenseInfo = () => 
  authService.makeAuthRequest('license/info');

// License features endpoint
export const getLicenseFeatures = () => 
  authService.makeAuthRequest('license/features');

// Activate license endpoint
export const activateLicense = async (licenseKey) => {
  const response = await authService.makeAuthRequest('license/activate', {
    method: 'POST',
    body: JSON.stringify({ license_key: licenseKey }),
  });

  clearLicenseStatusCache();
  return response;
};

// Deactivate license endpoint
export const deactivateLicense = async () => {
  const response = await authService.makeAuthRequest('license/deactivate', {
    method: 'POST',
  });

  clearLicenseStatusCache();
  return response;
};

// Device information endpoint
export const getDeviceInfo = () => 
  authService.makeAuthRequest('license/device-info');
