import { createSlice, createAsyncThunk } from '@reduxjs/toolkit';
import {
  fetchUsers as fetchUsersService,
  activateUser,
  deactivateUser,
  deleteUser,
  createUser,
  updatePassword,
} from '../../service';

export const fetchUsers = createAsyncThunk(
  'users/fetchUsers',
  async () => {
    const users = await fetchUsersService();
    return users;
  }
);

export const createUserAction = createAsyncThunk(
  'users/createUser',
  async (userData, { rejectWithValue }) => {
    try {
      await createUser(userData);
      const users = await fetchUsersService();
      return users;
    } catch (error) {
      return rejectWithValue(error.message || 'Failed to create user');
    }
  }
);

export const activateUserAction = createAsyncThunk(
  'users/activateUser',
  async (userId) => {
    await activateUser(userId);
    const users = await fetchUsersService();
    return users;
  }
);

export const deactivateUserAction = createAsyncThunk(
  'users/deactivateUser',
  async (userId) => {
    await deactivateUser(userId);
    const users = await fetchUsersService();
    return users;
  }
);

export const deleteUserAction = createAsyncThunk(
  'users/deleteUser',
  async (userId) => {
    await deleteUser(userId);
    const users = await fetchUsersService();
    return users;
  }
);

export const changePasswordAction = createAsyncThunk(
  'users/changePassword',
  async ({ userId, currentPassword, newPassword }, { rejectWithValue }) => {
    try {
      await updatePassword(userId, newPassword, currentPassword);
    } catch (error) {
      return rejectWithValue(error.message || 'Failed to update password');
    }
  }
);

const userSlice = createSlice({
  name: 'users',
  initialState: {
    data: [],
    loading: false,
    error: null,
    changingPassword: false,
    passwordError: null,
    creatingUser: false,
    createError: null,
  },
  reducers: {
    clearError: (state) => {
      state.error = null;
      state.passwordError = null;
      state.createError = null;
    },
  },
  extraReducers: (builder) => {
    builder
      // Fetch Users
      .addCase(fetchUsers.pending, (state) => {
        state.loading = true;
      })
      .addCase(fetchUsers.fulfilled, (state, action) => {
        state.data = action.payload;
        state.loading = false;
        state.error = null;
      })
      .addCase(fetchUsers.rejected, (state, action) => {
        state.loading = false;
        state.error = action.error.message;
      })

      // Create User
      .addCase(createUserAction.pending, (state) => {
        state.creatingUser = true;
        state.createError = null;
      })
      .addCase(createUserAction.fulfilled, (state, action) => {
        state.data = action.payload;
        state.creatingUser = false;
        state.createError = null;
      })
      .addCase(createUserAction.rejected, (state, action) => {
        state.creatingUser = false;
        state.createError = action.payload;
      })

      // Activate User
      .addCase(activateUserAction.fulfilled, (state, action) => {
        state.data = action.payload;
        state.error = null;
      })
      .addCase(activateUserAction.rejected, (state, action) => {
        state.error = action.error.message;
      })

      // Deactivate User
      .addCase(deactivateUserAction.fulfilled, (state, action) => {
        state.data = action.payload;
        state.error = null;
      })
      .addCase(deactivateUserAction.rejected, (state, action) => {
        state.error = action.error.message;
      })

      // Delete User
      .addCase(deleteUserAction.fulfilled, (state, action) => {
        state.data = action.payload;
        state.error = null;
      })
      .addCase(deleteUserAction.rejected, (state, action) => {
        state.error = action.error.message;
      })

      // Change Password
      .addCase(changePasswordAction.pending, (state) => {
        state.changingPassword = true;
        state.passwordError = null;
      })
      .addCase(changePasswordAction.fulfilled, (state) => {
        state.changingPassword = false;
        state.passwordError = null;
      })
      .addCase(changePasswordAction.rejected, (state, action) => {
        state.changingPassword = false;
        state.passwordError = action.payload.message || 'An error occurred while updating password';
      });
  },
});

export const { clearError } = userSlice.actions;
export default userSlice.reducer; 