import { useQuery } from '@tanstack/react-query';
import { getVpnStatus } from '../service/warhammer';

const useVpnStatusQuery = (options = {}) => {
  const { pausePolling = false, ...queryOptions } = options;
  
  return useQuery({
    queryKey: ['vpn-status'],
    queryFn: getVpnStatus,
    refetchInterval: pausePolling ? false : 10000, // Poll every 10 seconds, or pause if requested
    refetchIntervalInBackground: true,
    retry: (failureCount, error) => {
      // Retry up to 5 times with exponential backoff
      if (failureCount >= 5) return false;
      
      // Don't retry on 401/403 errors (auth issues)
      if (error?.status === 401 || error?.status === 403) return false;
      
      return true;
    },
    retryDelay: (attemptIndex) => Math.min(1000 * 2 ** attemptIndex, 30000),
    staleTime: 5 * 1000, // 5 seconds
    gcTime: 10 * 60 * 1000, // 10 minutes
    
    ...queryOptions,
  });
};

export default useVpnStatusQuery; 