import { createSlice, createAsyncThunk } from '@reduxjs/toolkit';
import { fetchPeers, fetchRoutes, createRoute, deleteRoute, deleteVpnPeer } from '../../service';
import _ from 'lodash';

const getTimezoneOffset = () => {
  // convert timezone to UTC offset
  const offset = new Date().getTimezoneOffset();
  const offsetHours = Math.abs(Math.floor(offset / 60));
  const offsetMinutes = Math.abs(offset % 60).toString().padStart(2, '0');
  const offsetSign = offset < 0 ? '+' : '-';
  return `${offsetSign}${offsetHours}:${offsetMinutes}`;
}

const formatTimestamp = (timestamp) => {
  if (!timestamp) {
    return null;
  }

  // convert `1740783037.0164242` to `15:30:37` 24 hour format
  const formattedTimestamp = new Date(timestamp * 1000).toLocaleTimeString('en-US', { timeZone: 'UTC', hour12: false });

  // return the timestamp with timezone
  return `${formattedTimestamp}${getTimezoneOffset()}`;
}

const formatPeerData = (peers) => {
  if (!Array.isArray(peers)) {
    console.error('formatPeerData: peers is not an array:', typeof peers, peers);
    return [];
  }
  
  return peers.map(peer => {
    // Handle the actual data structure from the API
    const formattedPeer = {
      ...peer,
      // Ensure we have the required fields
      id: peer.id || peer.name || `peer-${Math.random()}`,
      name: peer.name || peer.id || 'Unknown',
      ip: peer.ip || null,
      connected: peer.connected || false,
      location: {
        latitude: peer.location?.latitude || null,
        longitude: peer.location?.longitude || null,
        last_update: peer.location?.last_update ? formatTimestamp(peer.location.last_update) : null,
      },
      groups: peer.groups || [],
      last_seen: peer.last_seen || null,
    };
    
    return formattedPeer;
  });
};

export const getPeers = createAsyncThunk(
  'vpn/getPeers',
  async () => {
    const peers = await fetchPeers().then(formatPeerData);
    // sort by connection status then by name
    return peers.sort((a, b) => {
      if (a.connected === b.connected) {
        return a.name.localeCompare(b.name);
      }
      return a.connected ? -1 : 1;
    });
  }
);

export const getRoutes = createAsyncThunk(
  'vpn/getRoutes',
  async () => {
    const routes = await fetchRoutes();
    return routes;
  }
);

export const createRouteAction = createAsyncThunk(
  'vpn/createRoute',
  async (routeData) => {
    await createRoute(routeData);
    const routes = await fetchRoutes();
    return routes;
  }
);

export const deleteRouteAction = createAsyncThunk(
  'vpn/deleteRoute',
  async (routeId) => {
    await deleteRoute(routeId);
    const routes = await fetchRoutes();
    return routes;
  }
);

export const deleteVpnPeerAction = createAsyncThunk(
  'vpn/deleteVpnPeer',
  async (peerId) => {
    await deleteVpnPeer(peerId);
  }
);

const vpnSlice = createSlice({
  name: 'vpn',
  initialState: {
    peers: [],
    routes: [],
    loading: false,
    error: null,
    addingRoute: false,
    deletingRouteId: null,
  },
  reducers: {
    clearError: (state) => {
      state.error = null;
    },
    setError: (state, action) => {
      state.error = action.payload;
    },
  },
  extraReducers: (builder) => {
    builder
      .addCase(getPeers.fulfilled, (state, action) => {
        state.peers = action.payload;
        state.loading = false;
      })
      .addCase(getRoutes.fulfilled, (state, action) => {
        state.routes = action.payload;
        state.loading = false;
      })
      .addCase(createRouteAction.pending, (state) => {
        state.addingRoute = true;
      })
      .addCase(createRouteAction.fulfilled, (state, action) => {
        state.routes = action.payload;
        state.addingRoute = false;
        state.error = null;
      })
      .addCase(createRouteAction.rejected, (state, action) => {
        state.addingRoute = false;
        state.error = action.error.message;
      })
      .addCase(deleteRouteAction.pending, (state, action) => {
        state.deletingRouteId = action.meta.arg;
      })
      .addCase(deleteRouteAction.fulfilled, (state, action) => {
        state.routes = action.payload;
        state.deletingRouteId = null;
      })
      .addCase(deleteRouteAction.rejected, (state) => {
        state.deletingRouteId = null;
      })
      .addCase(deleteVpnPeerAction.pending, (state, action) => {
        state.deletingVpnPeerId = action.meta.arg;
      })
      .addCase(deleteVpnPeerAction.fulfilled, (state, action) => {
        state.peers = state.peers.filter(peer => peer.id !== state.deletingVpnPeerId);
      });
  },
});

export const { clearError, setError } = vpnSlice.actions;
export { formatPeerData };
export default vpnSlice.reducer; 