import 'react-app-polyfill/stable'
import 'core-js'
import React from 'react'
import { createRoot } from 'react-dom/client'
import { QueryClient, QueryClientProvider } from '@tanstack/react-query'
import App from './App'

// Conditionally import ReactQueryDevtools only in development
let ReactQueryDevtools = null;
if (process.env.NODE_ENV === 'development') {
  try {
    const { ReactQueryDevtools: DevTools } = require('@tanstack/react-query-devtools');
    ReactQueryDevtools = DevTools;
  } catch (error) {
    // Devtools not available, ignore
  }
}

// Create a client
const queryClient = new QueryClient({
  defaultOptions: {
    queries: {
      // Default polling configuration
      refetchOnWindowFocus: true,
      refetchOnReconnect: true,
      retry: (failureCount, error) => {
        // Retry up to 3 times with exponential backoff
        if (failureCount >= 3) return false;
        
        // Don't retry on 401/403 errors (auth issues)
        if (error?.status === 401 || error?.status === 403) return false;
        
        // Don't retry on timeout errors after 1 attempt
        if (error?.message?.includes('Request timed out') && failureCount >= 1) return false;
        
        return true;
      },
      retryDelay: (attemptIndex) => Math.min(1000 * 2 ** attemptIndex, 30000),
      staleTime: 5 * 1000, // 5 seconds
      gcTime: 10 * 60 * 1000, // 10 minutes (formerly cacheTime)
      // Network timeout - this works with the existing AbortController in utils.js
      networkMode: 'online',
    },
    mutations: {
      // Default mutation configuration
      retry: (failureCount, error) => {
        // Don't retry mutations on auth errors
        if (error?.status === 401 || error?.status === 403) return false;
        
        // Don't retry on timeout errors
        if (error?.message?.includes('Request timed out')) return false;
        
        // Retry up to 2 times for mutations
        return failureCount < 2;
      },
      retryDelay: (attemptIndex) => Math.min(1000 * 2 ** attemptIndex, 10000),
    },
  },
});

const container = document.getElementById('root')
const root = createRoot(container)

root.render(
  <QueryClientProvider client={queryClient}>
    <App />
    {ReactQueryDevtools && <ReactQueryDevtools initialIsOpen={false} />}
  </QueryClientProvider>
)
