import { useQuery, useMutation, useQueryClient } from '@tanstack/react-query';
import { 
  getLicenseStatus, 
  getLicenseInfo, 
  activateLicense, 
  deactivateLicense,
  getDeviceInfo
} from '../service/licenseService';

// Hook for license status
export const useLicenseStatusQuery = (options = {}) => {
  const { pausePolling = false, ...queryOptions } = options;
  
  return useQuery({
    queryKey: ['license-status'],
    queryFn: async () => {
      const startTime = Date.now();
      try {
        const status = await getLicenseStatus();
        const responseTime = Date.now() - startTime;
        
        // Log slow responses for monitoring
        if (responseTime > 5000) {
          console.warn(`Slow license status response: ${responseTime}ms`);
        }
        
        return status;
      } catch (error) {
        const responseTime = Date.now() - startTime;
        console.error(`License status query failed after ${responseTime}ms:`, error);
        throw error;
      }
    },
    
    // Adaptive polling based on data state
    refetchInterval: (query) => {
      // Pause polling if requested
      if (pausePolling) {
        return false;
      }
      
      const data = query.state.data;
      const error = query.state.error;
      const failureCount = query.state.failureCount;
      
      // If there's an error and we've failed multiple times, slow down
      if (error && failureCount > 1) {
        return Math.min(30000 * failureCount, 300000); // 30 seconds to 5 minutes
      }
      
      // If we have no data, poll more frequently
      if (!data) {
        return 5000; // 5 seconds
      }
      
      // Default polling interval
      return 30000; // 30 seconds
    },
    
    // Background polling and network awareness
    refetchIntervalInBackground: true,
    refetchOnWindowFocus: true,
    refetchOnReconnect: true,
    
    // Smart retry with jitter
    retry: (failureCount, error) => {
      if (error?.status === 401 || error?.status === 403) return false;
      if (error?.message?.includes('Network Error') && failureCount >= 2) return false;
      return failureCount < 3;
    },
    
    // Cache configuration
    staleTime: 10 * 1000, // 10 seconds
    gcTime: 5 * 60 * 1000, // 5 minutes
    
    ...queryOptions,
  });
};

// Hook for license information (only fetched when licensed)
export const useLicenseInfoQuery = (enabled = false, options = {}) => {
  const { pausePolling = false, ...queryOptions } = options;
  
  return useQuery({
    queryKey: ['license-info'],
    queryFn: async () => {
      const startTime = Date.now();
      try {
        const info = await getLicenseInfo();
        const responseTime = Date.now() - startTime;
        
        // Log slow responses for monitoring
        if (responseTime > 5000) {
          console.warn(`Slow license info response: ${responseTime}ms`);
        }
        
        return info;
      } catch (error) {
        const responseTime = Date.now() - startTime;
        console.error(`License info query failed after ${responseTime}ms:`, error);
        throw error;
      }
    },
    
    // Only run when enabled (when user is licensed)
    enabled,
    
    // Don't poll license info as frequently as status
    refetchInterval: pausePolling ? false : 60000, // 1 minute, or pause if requested
    
    // Background polling
    refetchIntervalInBackground: true,
    
    // Smart retry
    retry: (failureCount, error) => {
      if (error?.status === 401 || error?.status === 403) return false;
      return failureCount < 2;
    },
    
    // Cache configuration
    staleTime: 30 * 1000, // 30 seconds
    gcTime: 10 * 60 * 1000, // 10 minutes
    
    ...queryOptions,
  });
};

// Hook for license activation mutation
export const useActivateLicenseMutation = () => {
  const queryClient = useQueryClient();
  
  return useMutation({
    mutationFn: activateLicense,
    onSuccess: () => {
      // Invalidate and refetch license queries
      queryClient.invalidateQueries({ queryKey: ['license-status'] });
      queryClient.invalidateQueries({ queryKey: ['license-info'] });
    },
    onError: (error) => {
      console.error('License activation failed:', error);
    },
  });
};

