import { createSlice, createAsyncThunk } from '@reduxjs/toolkit';
import {
  getInterfaces,
  setInterfaceIp,
  removeInterfaceIp,
  getGsmConnections,
  createGsmConnection,
  activateConnection,
  deactivateConnection,
  deleteConnection,
  enableBridge,
  disableBridge,
  getGsmStatus,
  enableModem,
  disableModem,
} from '../../service';

export const addInterfaceIp = createAsyncThunk(
  'network/addIp',
  async ({ interfaceName, ip }) => {
    await setInterfaceIp(interfaceName, ip);
    const interfaces = await getInterfaces();
    return interfaces;
  }
);

// new
export const removeIp = createAsyncThunk(
  'network/removeIp',
  async ({ interfaceName, ip }) => {
    await removeInterfaceIp(interfaceName, ip);
    const interfaces = await getInterfaces();
    return interfaces;
  }
);

export const addGsmConnection = createAsyncThunk(
  'network/addGsmConnection',
  async (connection) => {
    await createGsmConnection(connection);
    const connections = await getGsmConnections();
    return connections;
  }
);

export const toggleGsmConnection = createAsyncThunk(
  'network/toggleGsmConnection',
  async ({ name, connected }) => {
    if (connected) {
      await deactivateConnection(name);
    } else {
      await activateConnection(name);
    }
    const connections = await getGsmConnections();
    return connections;
  }
);

export const deleteGsmConnection = createAsyncThunk(
  'network/deleteGsmConnection',
  async (name) => {
    await deleteConnection(name);
    const connections = await getGsmConnections();
    return connections;
  }
);

export const toggleBridge = createAsyncThunk(
  'network/toggleBridge',
  async (bridged) => {
    if (bridged) {
      await disableBridge();
    } else {
      await enableBridge();
    }
    const interfaces = await getInterfaces();
    return interfaces;
  }
);

export const toggleModem = createAsyncThunk(
  'network/toggleModem',
  async (isEnabled) => {
    if (isEnabled) {
      await disableModem();
    } else {
      await enableModem();
    }
    // Fetch updated status after toggling
    const response = await getGsmStatus();
    return response;
  }
);

const networkSlice = createSlice({
  name: 'network',
  initialState: {
    interfaces: {},
    gsmConnections: [],
    loading: {
      addIp: false,
      removeIp: false,
      addConnection: false,
      toggleConnection: false,
      deleteConnection: false,
      bridge: false,
      toggleModem: false,
    },
    error: null,
    bridged: false,
    gsmStatus: {},
  },
  reducers: {
    clearError: (state) => {
      state.error = null;
    },
  },
  extraReducers: (builder) => {
    builder
      // Add IP
      .addCase(addInterfaceIp.pending, (state) => {
        state.loading.addIp = true;
      })
      .addCase(addInterfaceIp.fulfilled, (state, action) => {
        state.interfaces = action.payload;
        state.loading.addIp = false;
        state.error = null;
      })
      .addCase(addInterfaceIp.rejected, (state, action) => {
        state.loading.addIp = false;
        state.error = action.error.message;
      })

      // Remove IP
      .addCase(removeIp.pending, (state) => {
        state.loading.removeIp = true;
      })
      .addCase(removeIp.fulfilled, (state, action) => {
        state.interfaces = action.payload;
        state.loading.removeIp = false;
        state.error = null;
      })
      .addCase(removeIp.rejected, (state, action) => {
        state.loading.removeIp = false;
        state.error = action.error.message;
      })

      // Add GSM Connection
      .addCase(addGsmConnection.pending, (state) => {
        state.loading.addConnection = true;
      })
      .addCase(addGsmConnection.fulfilled, (state, action) => {
        state.gsmConnections = action.payload;
        state.loading.addConnection = false;
        state.error = null;
      })
      .addCase(addGsmConnection.rejected, (state, action) => {
        state.loading.addConnection = false;
        state.error = action.error.message;
      })

      // Toggle GSM Connection
      .addCase(toggleGsmConnection.pending, (state) => {
        state.loading.toggleConnection = true;
      })
      .addCase(toggleGsmConnection.fulfilled, (state, action) => {
        state.gsmConnections = action.payload;
        state.loading.toggleConnection = false;
        state.error = null;
      })
      .addCase(toggleGsmConnection.rejected, (state, action) => {
        state.loading.toggleConnection = false;
        state.error = action.error.message;
      })

      // Delete GSM Connection
      .addCase(deleteGsmConnection.pending, (state) => {
        state.loading.deleteConnection = true;
      })
      .addCase(deleteGsmConnection.fulfilled, (state, action) => {
        state.gsmConnections = action.payload;
        state.loading.deleteConnection = false;
        state.error = null;
      })
      .addCase(deleteGsmConnection.rejected, (state, action) => {
        state.loading.deleteConnection = false;
        state.error = action.error.message;
      })

      // Toggle Bridge
      .addCase(toggleBridge.pending, (state) => {
        state.loading.bridge = true;
      })
      .addCase(toggleBridge.fulfilled, (state, action) => {
        state.interfaces = action.payload;
        state.loading.bridge = false;
        state.error = null;
        state.bridged = !state.bridged;
      })
      .addCase(toggleBridge.rejected, (state, action) => {
        state.loading.bridge = false;
        state.error = action.error.message;
      })

      // Toggle Modem
      .addCase(toggleModem.pending, (state) => {
        state.loading.toggleModem = true;
        state.error = null;
      })
      .addCase(toggleModem.fulfilled, (state, action) => {
        state.loading.toggleModem = false;
        state.gsmStatus = action.payload;
      })
      .addCase(toggleModem.rejected, (state, action) => {
        state.loading.toggleModem = false;
        state.error = action.error.message;
      });
  },
});

export const { clearError } = networkSlice.actions;
export default networkSlice.reducer; 