export const uploadFile = async (myFile, checksum = null) => {
  // Create a FormData object to send the file and checksum
  const formData = new FormData();
  formData.append('file', myFile);
  if (checksum) {
    formData.append('checksum', checksum);
  }

  // Don't set Content-Type header - let the browser set it with boundary
  return authService.makeAuthRequest(
    'upload',
    {
      method: 'POST',
      body: formData,
      headers: {}, // Remove Content-Type header completely
    },
    30000 // 30 second timeout for uploads
  );
};

