import { createSlice, createAsyncThunk } from '@reduxjs/toolkit';
import { uploadFile } from '../../service';

export const uploadUpdateFile = createAsyncThunk(
  'upload/updateFile',
  async ({ file, checksum }) => {
    const result = await uploadFile(file, checksum);
    return result;
  }
);

const uploadSlice = createSlice({
  name: 'upload',
  initialState: {
    loading: false,
    error: null,
    success: false,
  },
  reducers: {
    clearUploadState: (state) => {
      state.loading = false;
      state.error = null;
      state.success = false;
    },
  },
  extraReducers: (builder) => {
    builder
      .addCase(uploadUpdateFile.pending, (state) => {
        state.loading = true;
        state.error = null;
        state.success = false;
      })
      .addCase(uploadUpdateFile.fulfilled, (state) => {
        state.loading = false;
        state.error = null;
        state.success = true;
      })
      .addCase(uploadUpdateFile.rejected, (state, action) => {
        state.loading = false;
        state.error = action.error.message;
        state.success = false;
      });
  },
});

export const { clearUploadState } = uploadSlice.actions;
export default uploadSlice.reducer; 