import React from 'react';
import { SpinnerWrapper, Spinner, Message } from '../../styles/components/Common.styles';

const LoadingSpinner = ({ message = "Loading...", fixed = false, zIndex }) => (
  <SpinnerWrapper fixed={fixed} zIndex={zIndex}>
    <Spinner />
    <Message>{message}</Message>
  </SpinnerWrapper>
);

export default LoadingSpinner;