import { useQuery } from '@tanstack/react-query';
import { jwtDecode } from 'jwt-decode';
import authService from '../service/authService';

const useTokenExpirationQuery = (authToken, lastActivityRef) => {
  return useQuery({
    queryKey: ['token-expiration', authToken],
    queryFn: async () => {
      if (!authToken) return null;

      try {
        const { exp } = jwtDecode(authToken);
        const expiresIn = exp - Date.now() / 1000;
        const timeSinceLastActivity = (Date.now() - lastActivityRef.current) / 1000;
        const INACTIVITY_THRESHOLD = 30 * 60; // 30 minutes expressed in seconds
        const REFRESH_THRESHOLD = 5 * 60; // Refresh when 5 minutes remaining

        return {
          expiresIn,
          timeSinceLastActivity,
          isExpired: expiresIn <= 0,
          isInactive: timeSinceLastActivity > INACTIVITY_THRESHOLD,
          needsRefresh: expiresIn <= REFRESH_THRESHOLD && timeSinceLastActivity <= INACTIVITY_THRESHOLD,
          exp,
        };
      } catch (error) {
        console.error('Token validation failed:', error);
        throw error;
      }
    },
    
    // Check token expiration every minute
    refetchInterval: 60000, // 60 seconds
    refetchIntervalInBackground: true,
    
    // Only run when we have a token
    enabled: !!authToken,
    
    // Don't retry on token errors
    retry: false,
    
    // Cache configuration
    staleTime: 30 * 1000, // 30 seconds
    gcTime: 5 * 60 * 1000, // 5 minutes
    
    // Error handling
    onError: (error) => {
      console.error('Token expiration check failed:', error);
    },
    
    // Success handling
    onSuccess: (data) => {
      if (data) {
        console.debug('Token expiration check:', {
          expiresIn: Math.round(data.expiresIn),
          timeSinceLastActivity: Math.round(data.timeSinceLastActivity),
          needsRefresh: data.needsRefresh,
          isExpired: data.isExpired,
          isInactive: data.isInactive,
        });
      }
    },
  });
};

export default useTokenExpirationQuery; 