import React, { Suspense } from 'react';
import { Navigate, Route, Routes } from 'react-router-dom';
import { ThemeProvider } from 'styled-components';
import { theme } from '../styles/theme';
import LoadingSpinner from '../components/common/LoadingSpinner';

// Lazy load components
const DefaultLayout = React.lazy(() => import('../components/dashboard/DefaultLayout'));
const Login = React.lazy(() => import('../components/login/Login'));

const AppRoutes = () => {
  return (
    <ThemeProvider theme={theme}>
      <Suspense fallback={<LoadingSpinner fixed zIndex={2000} />}>
        <Routes>
          <Route path="/login" name="Login" element={<Login />} />
          <Route path="/dashboard" name="Dashboard" element={<DefaultLayout />} />
          <Route path="*" name="Redirect" element={<Navigate to="/dashboard" />} />
        </Routes>
      </Suspense>
    </ThemeProvider>
  );
};

export default AppRoutes; 