import { createSlice, createAsyncThunk } from '@reduxjs/toolkit';
import { getWarhammerInfo } from '../../service';

export const fetchWarhammerStatus = createAsyncThunk(
  'warhammer/fetchStatus',
  async () => {
    const info = await getWarhammerInfo();
    return info;
  }
);

const warhammerSlice = createSlice({
  name: 'warhammer',
  initialState: {
    data: {
      info: null,
      location: {
        lat: 0,
        lng: 0,
      },
    },
    loading: false,
    error: null,
  },
  reducers: {
    clearError: (state) => {
      state.error = null;
    },
  },
  extraReducers: (builder) => {
    builder
      .addCase(fetchWarhammerStatus.pending, (state) => {
        state.loading = true;
      })
      .addCase(fetchWarhammerStatus.fulfilled, (state, action) => {
        state.data.info = action.payload;
        state.loading = false;
        state.error = null;
      })
      .addCase(fetchWarhammerStatus.rejected, (state, action) => {
        state.loading = false;
        state.error = action.error.message;
      });
  },
});

export const { clearError } = warhammerSlice.actions;
export default warhammerSlice.reducer; 