import React, { createContext, useContext, useMemo } from 'react';
import { useLicenseStatusQuery } from '../hooks/useLicenseQueries';
import { usePollingPause } from './PollingPauseContext';

const LicenseContext = createContext(null);

export const LicenseProvider = ({ children }) => {
  const { isPollingPaused } = usePollingPause();
  const { data: licenseStatusData, isLoading, error } = useLicenseStatusQuery({ 
    pausePolling: isPollingPaused 
  });
  
  const licenseStatus = licenseStatusData?.status || 'unknown';
  
  // Memoize the license enforcement logic to avoid unnecessary recalculations
  const licenseEnforcement = useMemo(() => {
    const isLicensed = licenseStatus === 'licensed';
    const isExpired = licenseStatus === 'expired';
    const isUnlicensed = licenseStatus === 'unlicensed';
    
    return {
      // License status
      licenseStatus,
      isLoading,
      error,
      
      // Boolean flags for easy checking
      isLicensed,
      isExpired,
      isUnlicensed,
      
      // Access control functions
      canAccessPeers: isLicensed,
      canAccessMap: isLicensed,
      canAccessRoutes: isLicensed,
      canAccessNetwork: isLicensed,
      canAccessPerformance: isLicensed,
      
      // Configuration access control
      canAccessSystemConfig: isLicensed,
      canAccessUserManagement: isLicensed,
      canAccessVpnKey: isLicensed,
      canAccessFileUpload: isLicensed,
      
      // Always allow these regardless of license status
      canAccessLicenseManagement: true,
      canAccessPasswordChange: true,
      canAccessLogout: true,
      
      // Helper function to check if user can access a specific feature
      canAccess: (feature) => {
        const accessMap = {
          peers: isLicensed,
          map: isLicensed,
          routes: isLicensed,
          network: isLicensed,
          performance: isLicensed,
          systemConfig: isLicensed,
          userManagement: isLicensed,
          vpnKey: isLicensed,
          fileUpload: isLicensed,
          licenseManagement: true,
          passwordChange: true,
          logout: true,
        };
        
        return accessMap[feature] || false;
      },
      
      // Get appropriate message for license status
      getLicenseMessage: () => {
        switch (licenseStatus) {
          case 'licensed':
            return ' Licensed - All features available';
          case 'expired':
            return ' License Expired - Please renew to access features';
          case 'unlicensed':
            return ' Unlicensed - Please activate a license to access features';
          default:
            return ' Unknown License Status';
        }
      },
      
      // Check if user should see restricted content
      shouldShowRestrictedContent: (userRoles = []) => {
        const isAdmin = userRoles.includes('admin');
        
        // Admins can see restricted content only when licensed
        if (isAdmin) {
          return isLicensed;
        }
        
        // Regular users can never see restricted content
        return false;
      }
    };
  }, [licenseStatus, isLoading, error]);

  return (
    <LicenseContext.Provider value={licenseEnforcement}>
      {children}
    </LicenseContext.Provider>
  );
};

export const useLicense = () => {
  const context = useContext(LicenseContext);
  if (!context) {
    throw new Error('useLicense must be used within a LicenseProvider');
  }
  return context;
};
